import React from "react";
import brexLogo from "assets/img/logos/brex/logo-black.svg";
import StartupValueProps from "components/personalized-landing-page/StartupValueProps.jsx";
import InternalLink from "components/reusable/atoms/links/InternalLink";
import SignUpReferenceLink from "components/reusable/atoms/links/SignUpReferenceLink";
import CompanyTypes from "utils/companyTypes";

const companyTypeToPath = {
  startup: "startups",
  lifescience: "industry/life-sciences",
  ecommerce: "ecommerce",
};

const Description = ({
  title = "The first corporate card for startups.",
  logoImg = brexLogo,
  logoClass = "brex-logo",
  descriptionText = "Nothing should hold your startup back. Thats why we built Brex: the card for startups with higher limits, no personal guarantee or security deposit required, and an online application that takes two minutes.",
  ctaCaption = "Get Your Card",
  learnMoreCta = true,
  learnMoreURL = "/startups/",
  legalNotice = "",
  statementCredit = "",
  companyType = CompanyTypes.STARTUP,
  ValueProps = StartupValueProps,
}) => (
  <section className="partner-landing-description">
    <InternalLink
      to={`/${companyTypeToPath[companyType.toLowerCase()]}/` || "/"}
    >
      <img alt={brexLogo} className={logoClass} src={logoImg} />
    </InternalLink>
    <div className="description-title">{title}</div>
    <div className="description-subtitle">{descriptionText}</div>
    <div className="personalized-landing--buttons-container">
      <SignUpReferenceLink
        companyType={companyType}
        className="orange-button orange-button--getcard personalized-landing--button"
      >
        {ctaCaption}
      </SignUpReferenceLink>
      {learnMoreCta && (
        <InternalLink
          to={learnMoreURL}
          className="orange-button orange-button--getcard personalized-landing--button"
        >
          Learn More
        </InternalLink>
      )}
    </div>
    <ValueProps
      statementCredit={statementCredit}
      className="landing-description--personalized__valueprops"
    />
    {legalNotice && (
      <div className="partner-landing--legal partner-landing--legal__disclaimer">
        {legalNotice}
      </div>
    )}
  </section>
);

export default Description;
